import { AuthStore } from '../../../lib/auth';

export const requireAuth = (returnPath) => (from, to, next) => {
    const loggedIn = AuthStore.state.loggedIn;
    if (loggedIn) {
        return next();
    }
    if (from.params.id === undefined) {
        return next(`/signin?returnPath=${returnPath}`);    
    }
    next(`/signin?returnPath=${returnPath}/${from.params.id}`);
}