<template>
    <div id="create-exam">
        <h1>공사중</h1>
        <div id="title">
            <input type="text" id="examTitle" v-model="examTitle">
            <button v-on:click="createExam()">생성</button>
        </div>
        <hr>
        <b-form-textarea id="examDescription" rows="1" max-rows="8" v-model="examDescription"></b-form-textarea>
        <hr>
        <div id="problems">
            <h3 style="font-weight: bold;">문제</h3>
            <ul v-for="(problem, index) in problems" v-bind:key="index">
                <li>
                    <div class="problem">
                        <span>{{ problem.problemTitle }}</span>
                        <button v-on:click="modifyProblem(index)">수정</button>
                        <button v-on:click="deleteProblem(index)">삭제</button>
                    </div>
                </li>
            </ul>
            <div id="add-problem-container">
                <span v-on:click="showModal()">문제 추가하기</span>
                <!-- modal section -->
                <b-modal id="add-problem" ref="modal" @show="showModal" @hidden="resetModal" @ok="modalOk">
                    <form @submit.stop.prevent="addProblem">
                        <h5>문제 제목</h5>
                        <b-form-input v-model="problemTitle"></b-form-input>
                        <h5>문제 설명</h5>
                        <b-form-textarea v-model="problemDescription"></b-form-textarea>
                        <h5>테스트 케이스</h5>
                        <table>
                            <tr>
                                <td>입력</td>
                                <td>출력</td>
                            </tr>
                            <tr v-for="(testcase, index) in testcases" v-bind:key="index">
                                <td>
                                    <b-form-textarea v-model="testcase.input"></b-form-textarea>
                                </td>
                                <td>
                                    <b-form-textarea v-model="testcase.output"></b-form-textarea>
                                </td>
                                <b-button size="sm" v-on:click="deleteTestCase(index)">삭제</b-button>
                            </tr>
                        </table>
                        <b-button size="sm" v-on:click="addTestCase()">추가</b-button>
                        <h5>CPU 시간 제한</h5>
                        <input type="text" v-model="cpuTimeLimit">
                        <h5>실제 시간 제한</h5>
                        <input type="text" v-model="wallTimeLimit">
                        <h5>메모리 제한</h5>
                        <input type="text" v-model="memoryLimit">
                    </form>
                </b-modal>
                <!-- modal section -->
            </div>
        </div>
        <hr>
        <div id="other-settings">
            <h3 style="font-weight: bold;">기타 설정</h3>
            <table>
                <tr>
                    <td>
                        <span>시간 제한 (초)</span>
                    </td>
                    <td>
                        <span>공개 날짜</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <input type="number" v-model="examTimeLimit">
                    </td>
                    <td>
                        <input type="datetime-local" v-model="examOpenDate">
                    </td>
                </tr>    
            </table>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            examTitle: "",
            examDescription: "",
            examTimeLimit: 0,
            examOpenDate: null,
            problemTitle: "",
            problemDescription: "",
            // TODO: solvingTimeLimit field 삭제 예정. -> ExamTimeLimit으로 대체.
            wallTimeLimit: 0,
            cpuTimeLimit: 0,
            memoryLimit: 0,
            // TODO(solved): openDate field 삭제 예정. -> ExamOpenDate로 대체.
            problems: [],
            testcases: []
        }
    },
    methods: {
        showModal () {
            console.log("showModal()");
            this.$bvModal.show("add-problem");
        },
        modalOk (bvModalEvt) {
            console.log("modalOk()");
            bvModalEvt.preventDefault();
            this.addProblem();
        },
        addProblem () {
            console.log("addProblem()");
            this.problems.push({
                // problem info
                problemTitle: this.problemTitle,
                problemDescription: this.problemDescription,
                wallTimeLimit: this.wallTimeLimit,
                cpuTimeLimit: this.cpuTimeLimit,
                memoryLimit: this.memoryLimit,
                openDate: this.examOpenDate,
                testcases: this.testcases
            });

            this.$nextTick(() => {
                this.$bvModal.hide("add-problem");
            });
        },
        modifyProblem () {
            console.log("modifyProblem");
        },
        deleteProblem (problemId) {
            console.log("deleteProblem()");
            this.problems.splice(problemId, 1);
        },
        addTestCase () {
            console.log("addTestCase()");
            this.testcases.push({
                "input": "",
                "output": ""
            });
        },
        deleteTestCase (testcase_id) {
            console.log("deleteTestCase()");
            this.testcases.splice(testcase_id, 1);
        },
        resetModal () {
            console.log("resetModal()");
            this.problemTitle = ""
            this.problemDescription = ""
            // TODO (보류): solvingTimeLimit field 삭제 예정. -> ExamTimeLimit으로 대체.
            this.solvingTimeLimit = 0
            this.wallTimeLimit = 0
            this.cpuTimeLimit = 0
            this.memoryLimit = 0
            this.openDate = null
            this.testcases = []
        },
        async createExam () {
            const exam_context = {
                "title": this.examTitle,
                "description": this.examDescription,
                "solving_time_limit": this.examTimeLimit,
                "start_date": this.examOpenDate
            };

            try {
                const exam_response = await axios.post('/exams', exam_context);

                this.problems.forEach(async item => {
                    const problem_response = await axios.post('/problems', {
                        "title": item.problemTitle,
                        "description": item.problemDescription,
                        "cpu_time_limit": item.cpuTimeLimit,
                        "wall_time_limit": item.wallTimeLimit,
                        "exam": exam_response.data.id,
                        "memory_limit": item.memoryLimit,
                        // TODO (완료): open_date field 가 null로 인식되는 이유?
                        "open_date": exam_response.data.start_date
                    });
                    item.testcases.forEach(async item => {
                        await axios.post(`/problems/${problem_response.data.id}/testcases`, {
                            "input": item.input,
                            "output": item.output
                        });
                    });
                });

                this.$router.push('/');
            } catch (error) {
                console.error(error);
            }
        }
    }
}
</script>

<style scoped>
#examTitle {
    display: block;
    font-weight: bold;
    font-size: 1.75em;
    border: 0;
}

#create-exam {
    padding: 50px;
}

#description {
    padding-top: 5px;
    padding-bottom: 5px;
}

td {
    padding-top: 10px;
    padding-right: 10px;
}
</style>