import axios from 'axios';

const initState = JSON.parse(sessionStorage.getItem("loggedIn"));

export const AuthStore = {
    state: {
        loggedIn: initState
    },
    actions: {
        async login({ commit }, user) {
            try {
                const res = await Auth.login(user);
                commit("loginSuccess");
                return Promise.resolve(res);
            } catch (error) {
                commit("loginFailure");
                return Promise.reject(error);
            }
        },
        // TODO: 로그아웃 시 '/'로 이동.
        logout({ commit }) {
            Auth.logout().then(res => {
                commit("logout");
                return Promise.resolve(res);
            }).catch(error => {
                commit("logout");
                return Promise.reject(error);
            });
        },
        async register({ commit }, user) {
            try {
                const res = await Auth.register(user);
                commit("logout");
                return Promise.resolve(res);
            } catch (error) {
                commit("logout");
                return Promise.reject(error);
            }
        }
    },
    mutations: {
        loginSuccess(state) {
            state.loggedIn = true;
        },
        loginFailure(state) {
            state.loggedIn = false;
        },
        logout(state) {
            state.loggedIn = false;
        }
    },
    getters: {
        loggedIn: (state) => () => {
            return state.loggedIn;
        }
    }
};

export class Auth {
    static login(user) {
        return new Promise((resolve, reject) => {
            axios.post('/auth', {
                "username": user.username,
                "password": user.password
            }).then(res => {
                sessionStorage.setItem("loggedIn", true);
                resolve(res);
            }).catch(err => {
                sessionStorage.removeItem("loggedIn");
                reject(err);
            });
        });
    }
    static logout() {
        return new Promise((resolve, reject) => {
            axios.delete('/auth').then(res => {
                sessionStorage.removeItem("loggedIn");
                resolve(res);
            }).catch(err => {
                sessionStorage.removeItem("loggedIn");
                reject(err);
            });
        });
    }
    static register(user) {
        return new Promise((resolve, reject) => {
            axios.post('/users', {
                "username": user.username,
                "password": user.password,
                "email": user.email
            }).then(res => {
                sessionStorage.removeItem("loggedIn");
                resolve(res);
            }).catch(err => {
                sessionStorage.removeItem("loggedIn");
                reject(err);
            });
        });
    }
    static loggedIn() {
        return sessionStorage.getItem("loggedIn");
    }
}