import Sandbox from '../components/Sandbox';
import CreateProblem from '../components/CreateProblem';
import CreateExam from '../components/CreateExam';

import { requireAuth } from './navguard/guard';

export default [
    {
        path: '/create/snippet',
        name: 'create.snippet',
        component: Sandbox
    },
    {
        path: '/create/problem',
        name: 'create.problem',
        component: CreateProblem,
        beforeEnter: requireAuth("/create/problem")
    },
    {
        path: '/create/exam',
        name: 'create.exam',
        component: CreateExam,
        beforeEnter: requireAuth("/create/exam")
    }
];