import My from '../components/My';
import MyInfo from '../components/MyInfo';
import MyEntities from '../components/MyEntities';
import Submissions from '../components/Submissions';

import { requireAuth } from './navguard/guard';

export default [
    {
        path: '/my',
        name: 'my',
        component: My,
        beforeEnter: requireAuth("/my"),
        children: [
            {
                path: 'info',
                name: 'my.info',
                component: MyInfo
            },
            {
                path: 'problems',
                name: 'my.problems',
                component: MyEntities,
                props: {
                    title: "생성한 문제",
                    apiRoute: "problems"
                }
            },
            {
                path: 'exams',
                name: 'my.exams',
                component: MyEntities,
                props: {
                    title: "생성한 시험",
                    apiRoute: "exams"
                }
            },
            {
                path: 'attempts',
                name: 'my.attempts',
                component: Submissions,
                props: {
                    title: "시도한 문제",
                    apiRoute: "attempts"
                }
            },
            {
                path: 'solved',
                name: 'my.solved',
                component: Submissions,
                props: {
                    title: "해결한 문제",
                    apiRoute: "solved"
                }
            }
        ]
    }
];