<template>
    <div>
        <h2>{{ title }}</h2>
        <table class="table">
            <thead>
                <th>제목</th>
                <th>수정</th>
                <th>삭제</th>
            </thead>
            <tbody>
                <tr v-for="(entity, index) in entities" v-bind:key="index">
                    <td>
                        <router-link :to="{ name: 'problem', params: { id: entity.id }}">{{ entity.title}}</router-link>
                    </td>
                    <td>
                        <Button body="수정" :to="{ name: 'update.problem', params: { id: entity.id }}" class="btn-secondary"></Button>
                    </td>
                    <td>
                        <button type="button" v-on:click="deleteData(entity.id)" class="btn btn-danger">삭제</button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import axios from 'axios';
import Button from './reusable/button';

export default {
    components: {
        Button
    },
    props: {
        title: {
            type: String,
            default: ''
        },
        apiRoute: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            entities: []
        }
    },
    created() {
        this.fetchData();
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            axios.get(`/my/${this.apiRoute}`).then((entities_response) => {
                console.log(this.apiRoute);
                this.entities = entities_response.data;
            }).catch(error => console.error(error));
        },
        deleteData (id) {
            axios.delete(`/${this.apiRoute}/${id}`).then(delete_response => {
                console.log(delete_response);
            }).catch(error => console.error(error));
        }
    }
}
</script>