<template>
    <div id="update-problem">
        <div id="title">
            <input type="text" id="problemTitle" v-model="problemTitle">
            <button v-on:click="updateProblem()" type="button" class="btn btn-primary">저장</button>
        </div>
        <hr>
        <textarea class="form-control" v-model="problemDescription" required></textarea>
        <hr>
        <div id="test-cases">
            <h3 style="font-weight: bold;">테스트 케이스</h3>
            <table class="table">
                <tr>
                    <th>입력</th>
                    <th>출력</th>
                </tr>
                <tr v-for="(testcase, index) in testcases" v-bind:key="index">
                    <td>
                        <textarea class="form-control" v-model="testcase.input"></textarea>
                    </td>
                    <td>
                        <textarea class="form-control" v-model="testcase.output"></textarea>
                    </td>
                    <button v-on:click="deleteTestCase(index)" type="button" class="btn btn-danger deleteTC">삭제</button>
                </tr>
                <button v-on:click="addTestCase()" type="button" class="btn btn-primary">테스트 추가</button>
            </table>
        </div>
        <hr>
        <div id="other-settings">
            <h3 style="font-weight: bold;">기타 설정</h3>
            <table>
                <tr>
                    <td>
                        <span>CPU 시간 제한(초 단위, float)</span>
                    </td>
                    <td>
                        <span>실제 시간 제한(초 단위, float)</span>
                    </td>
                    <td>
                        <span>메모리 제한(KB)</span>
                    </td>
                    <td>
                        <span>공개 날짜(datetime)</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <input type="number" class="form-control" v-model="cpuTimeLimit">
                    </td>
                    <td>
                        <input type="number" class="form-control" v-model="wallTimeLimit">
                    </td>
                    <td>
                        <input type="number" class="form-control" v-model="memoryLimit">
                    </td>
                    <td>
                        <input type="datetime-local" class="form-control" step="1" v-model="openDate">
                    </td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    props: {
        id: null
    },
    data() {
        return {
            problemTitle: "",
            problemDescription: "",
            cpuTimeLimit: 0,
            wallTimeLimit: 0,
            memoryLimit: 0,
            openDate: null,
            testcases: [],
            deletedTestCases: []
        }
    },
    created() {
        const requests = [
            axios.get(`/problems/${this.id}`),
            axios.get(`/problems/${this.id}/testcases`)
        ];
        Promise.all(requests).then(responses => {
            console.log(responses);
            this.problemTitle = responses[0].data.title;
            this.problemDescription = responses[0].data.description;
            this.cpuTimeLimit = responses[0].data.cpu_time_limit;
            this.wallTimeLimit = responses[0].data.wall_time_limit;
            this.memoryLimit = responses[0].data.memory_limit;
            this.openDate = this.formatDatetime(responses[0].data.open_date);
            this.testcases = responses[1].data;
        }).catch(error => console.error(error));
    },
    methods: {
        formatDatetime (datestr) {
            const splited_str = datestr.split(" ");
            return splited_str.join("T");
        },
        addTestCase () {
            this.testcases.push({
                id: this.testcases.length,
                input: "",
                output: "",
                created: true
            });
        },
        deleteTestCase (testcase_id) {
            const removed_item = this.testcases.splice(testcase_id, 1)[0];
            if (!(removed_item.created)) {
                // 생성 예정이지만 생성되지 않음.
                this.deletedTestCases.push(removed_item);
            }
        },
        async updateProblem () {
            const problem_context = {
                "title": this.problemTitle,
                "description": this.problemDescription,
                "cpu_time_limit": this.cpuTimeLimit,
                "wall_time_limit": this.wallTimeLimit,
                "memory_limit": this.memoryLimit,
                "open_date": this.openDate
            };

            try {
                await axios.put(`/problems/${this.id}`, problem_context);
                
                this.testcases.forEach(item => {
                    if (item.created) {
                        // 생성
                        axios.post(`/problems/${this.id}/testcases`, {
                            "problem": this.id,
                            "input": item.input,
                            "output": item.output
                        }).then();
                        // TOOD: UnhandledPromiseRejection 확인하기.
                    } else {
                        // 갱신
                        axios.put(`/problems/${this.id}/testcases/${item.id}`, {
                            "problem": this.id,
                            "input": item.input,
                            "output": item.output
                        }).then();
                        // TOOD: UnhandledPromiseRejection 확인하기.
                    }
                });

                this.deletedTestCases.forEach(item => {
                    // 삭제
                    // TOOD: UnhandledPromiseRejection 확인하기.
                    axios.delete(`/problems/${this.id}/testcases/${item.id}`).then();
                });

                this.$router.push('/');
            } catch (error) {
                console.error(error);
            }
        }
    }
}
</script>

<style scoped>
#update-problem {
    padding: 50px;
}

#problemTitle {
    display: block;
    font-weight: bold;
    font-size: 1.75em;
    border: 0;
}

#description {
    padding-top: 5px;
    padding-bottom: 5px;
}

.deleteTC {
    margin-top: 23px;
}

td {
    padding-top: 10px;
    padding-right: 10px;
}

</style>