<template>
    <div id="submissions">
        <h2>{{ title }}</h2>
        <table class="table">
            <thead>
                <tr>
                    <th>문제</th>
                    <th>채점 결과</th>
                    <th>언어</th>
                    <th>제출 코드</th>
                    <th>제출 일자</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(submission, index) in submissions" v-bind:key="index">
                    <td>
                        <router-link :to="{ name: 'problem', params: { id: submission.problem }}">
                            {{ submission.problem_title }}
                        </router-link>
                    </td>
                    <td>{{ submission.result }}</td>
                    <td>{{ languageIdToChar(submission.language) }}</td>
                    <td>
                        <button type="button" v-on:click="clickedItem(index)" class="btn btn-primary" data-toggle="modal" data-target="#exampleModal">코드 보기</button>
                    </td>
                    <td>{{ submission.created_at }}</td>
                </tr>
            </tbody>
            <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog" role="document">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title" id="exampleModalLabel">제출한 코드</h5>
                        </div>
                        <div class="modal-body">
                            <pre>{{ codeOfclickedItem }}</pre>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                        </div>
                    </div>
                </div>
            </div>
        </table>
    </div>
</template>

<script>
import axios from 'axios';
import { languages } from '../../lib/languages';

export default {
    props: {
        title: {
            type: String,
            default: ''
        },
        apiRoute: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            submissions: [],
            codeOfclickedItem: ""
        }
    },
    created() {
        this.fetchData();
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            axios.get(`/my/${this.apiRoute}`).then(submissions_response => {
                this.submissions = submissions_response.data;
            }).catch(error => console.error(error));
        },
        languageIdToChar (language_id) {
            language_id = language_id.toString();
            return languages[language_id].name;
        },
        clickedItem (index) {
            this.codeOfclickedItem = this.submissions[index].code;
        }
    }
}
</script>

<style scoped>
.table {
    margin-top: 20px;
}
</style>