import Vue from 'vue'
import VueRouter from 'vue-router'

import create from './create';
import my from './my';
import menu from './menu';
import update from './update';

import { requireAuth } from './navguard/guard';

Vue.use(VueRouter);

const baseRoutes = [
    {
        path: '/signin',
        name: 'signin',
        props: {
            title: "signin"
        },
        component: () => import('../components/Account.vue')
    },
    {
        path: '/signup',
        name: 'signup',
        props: {
            title: "signup"
        },
        component: () => import('../components/Account.vue')
    },
    {
        path: '/snippet/:id',
        name: 'snippet',
        component: () => import('../components/Sandbox.vue')
    },
    {
        path: '/problem/:id',
        name: 'problem',
        props: true,
        component: () => import('../components/Problem.vue'),
        beforeEnter: requireAuth("/problem")
    }
];

const routes = baseRoutes.concat(menu, create, my, update);

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router;
