<template>
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
        <router-link to="/" id="title">Code::bin</router-link>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav mr-auto" v-if="currentLocationIsHome">
                <li class="nav-item dropdown">
                    <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    생성
                    </a>
                    <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                        <router-link class="dropdown-item" to="/create/snippet">스니펫 생성</router-link>
                        <router-link class="dropdown-item" to="/create/problem">문제 생성</router-link>
                        <router-link class="dropdown-item" to="/create/exam">시험 생성</router-link>
                    </div>
                </li>
                <li class="nav-item">
                    <a class="nav-link" v-on:click="signout" v-if="loggedIn">로그아웃</a>
                    <router-link class="nav-link" to="/signin" v-else>로그인</router-link>
                </li>
                <li class="nav-item">
                    <router-link class="nav-link" to="/my" v-if="loggedIn">내 계정</router-link>
                </li>
            </ul>
        </div>
    </nav>
</template>

<script>
export default {
    data() {
        return {
            currentLocationIsHome: false
        }
    },
    computed: {
        loggedIn () {
            return this.$store.state.loggedIn;
        }
    },
    watch: {
        $route() {
            this.getCurrentLocation();
        }
    },
    methods: {
        signout () {
            this.$store.dispatch('logout').then(() => {
                console.log("logged out");
            }).catch(error => console.error(error));
        },
        getCurrentLocation () {
            if (window.location.pathname === "/problems" || window.location.pathname === "/exams") return this.currentLocationIsHome = true;
            this.currentLocationIsHome = false;
        }
    }
}
</script>

<style scoped>

#title {
    display: inline;
    text-decoration: none;
    color: black;
    font-size: 18px;
}

nav {
    background-color: #e0e0e0;
}

#logout {
    color: red;
    position: absolute;
    right: 0;
    margin-right: 15px;
}

</style>