<template>
    <div id="create-problem">
        <div id="title">
            <input type="text" id="problemTitle" v-model="problemTitle">
            <button v-on:click="createProblem()" type="button" class="btn btn-primary">생성</button>
        </div>
        <hr>
        <textarea class="form-control" v-model="problemDescription" required></textarea>
        <hr>
        <div id="test-cases">
            <h3 style="font-weight: bold;">테스트 케이스</h3>
            <table class="table">
                <tr>
                    <th>입력</th>
                    <th>출력</th>
                </tr>
                <tr v-for="(testcase, index) in testcases" v-bind:key="index">
                    <td>
                        <textarea class="form-control" v-model="testcase.input"></textarea>
                    </td>
                    <td>
                        <textarea class="form-control" v-model="testcase.output"></textarea>
                    </td>
                    <button v-on:click="deleteTestCase(index)" type="button" class="btn btn-danger deleteTC">삭제</button>
                </tr>
                <button v-on:click="addTestCase()" type="button" class="btn btn-primary">테스트 추가</button>
            </table>
        </div>
        <hr>
        <div id="other-settings">
            <h3 style="font-weight: bold;">기타 설정</h3>
            <table>
                <tr>
                    <td>
                        <span>CPU 시간 제한(초 단위, float)</span>
                    </td>
                    <td>
                        <span>실제 시간 제한(초 단위, float)</span>
                    </td>
                    <td>
                        <span>메모리 제한(KB)</span>
                    </td>
                    <td>
                        <span>공개 날짜(datetime)</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <input type="number" class="form-control" v-model="cpuTimeLimit">
                    </td>
                    <td>
                        <input type="number" class="form-control" v-model="wallTimeLimit">
                    </td>
                    <td>
                        <input type="number" class="form-control" v-model="memoryLimit">
                    </td>
                    <td>
                        <input type="datetime-local" class="form-control" step="1" v-model="openDate">
                    </td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import { getTZ } from "../../lib/tz";

export default {
    data() {
        return {
            problemTitle: "",
            problemDescription: "",
            cpuTimeLimit: 0,
            wallTimeLimit: 0,
            memoryLimit: 0,
            openDate: null,
            testcases: []
        }
    },
    methods: {
        addTestCase () {
            this.testcases.push({
                id: this.testcases.length,
                input: "",
                output: ""
            });
        },
        deleteTestCase (testcase_id) {
            this.testcases.splice(testcase_id, 1);
        },
        async createProblem () {
            const problem_context = {
                "title": this.problemTitle,
                "description": this.problemDescription,
                "cpu_time_limit": this.cpuTimeLimit,
                "wall_time_limit": this.wallTimeLimit,
                "memory_limit": this.memoryLimit,
                // TODO: UTC와 Time Zone간의 offset구해서 api 서버로 넘기기. -> query로 CONVERT_TO함수 이용해 utc로 변환. -> get할 때 utc에서 client timezone으로 CONVERT_TO이용해 변환.
                "open_date": this.openDate,
                "exam": null,
                "tz": getTZ()
            };
            console.log(problem_context.open_date);
            try {
                const problem_response = await axios.post("/problems", problem_context);
                
                this.testcases.forEach(item => {
                    axios.post(`/problems/${problem_response.data.id}/testcases`, {
                        "problem": problem_response.data.id,
                        "input": item.input,
                        "output": item.output
                    }).then(() => {
                        
                    });
                });
                this.$router.push('/');
            } catch (error) {
                console.error(error);
            }
        }
    }
}
</script>

<style scoped>
#create-problem {
    padding: 50px;
}

#problemTitle {
    display: block;
    font-weight: bold;
    font-size: 1.75em;
    border: 0;
}

#description {
    padding-top: 5px;
    padding-bottom: 5px;
}

.deleteTC {
    margin-top: 23px;
}

td {
    padding-top: 10px;
    padding-right: 10px;
}

</style>